$sides: top, right, bottom, left;
$margins: 1, 2, 3, 4, 5, 10, 15, 20, 30, 40, 50, 80;
$paddings: 1, 2, 3, 4, 5, 10, 15, 20, 30, 40, 50, 80;
$sizes: 5, 10, 15, 20, 50, 80, 100, 150, 200, 250, 300, 400, 500, 600, 700, 800,
  900, 1000;
$colors: red, green, blue, yellow, pink, orange, purple;
$bg-primary: #0c55b4;
$bg-primary-lite: #397bd1;
$bg-primary-alt: #99b033;
$bg-primary-alt-lite: #b2c26e;
$bg-grey: rgb(170, 169, 169);

// Fonts *********************************************
@font-face {
  font-family: OpenSans;
  src: url(./OpenSans-Light.ttf);
}

@font-face {
  font-family: OpenSansRegular;
  src: url(./OpenSans-Regular.ttf);
}

@font-face {
  font-family: Code;
  src: url(./SourceCodePro-Medium.ttf);
}
@for $i from 1 through 15 {
  .fs#{$i} {
    font-size: 1+ ($i/10) em;
  }
}
// body   ********************************************
body {
  font-family: OpenSans;
  font-size: 14pt;
  margin: 0px;
  padding: 0px;
}
main {
  padding: 0 20px 0 20px;
}
// colors ********************************************
@each $color in $colors {
  .color-#{$color} {
    color: #{$color};
  }
}
@each $bgcolor in $colors {
  .bgcolor-#{$bgcolor} {
    background-color: #{$bgcolor};
  }
}
.bg-primary-alt {
  background-color: $bg-primary-alt;
}
.bg-primary-alt-lite {
  background-color: $bg-primary-alt-lite;
}
.primary-alt {
  color: $bg-primary-alt;
}
.primary-alt-lite {
  color: $bg-primary-alt-lite;
}

// flex  *********************************************
.flex {
  display: flex;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.reset {
  margin: 0px;
  padding: 0px;
}

// images ********************************************
.img-avatar {
  border-radius: 50%;
}
.img-rounded {
  border-radius: 10px;
}
// paddings, margins *********************************

@each $side in $sides {
  @each $margin in $margins {
    .m#{str-slice($side, 1,1)}-#{$margin} {
      margin-#{$side}: #{$margin}px;
    }
  }
}
@each $margin in $margins {
  .m-#{$margin} {
    margin: #{$margin}px;
  }
}

@each $side in $sides {
  @each $padding in $paddings {
    .p#{str-slice($side, 1,1)}-#{$padding} {
      padding-#{$side}: #{$padding}px;
    }
  }
}
@each $padding in $paddings {
  .p-#{$padding} {
    padding: #{$padding}px;
  }
}
@each $width in $sizes {
  .w-#{$width} {
    width: #{$width}px;
  }
}
@each $height in $sizes {
  .h-#{$height} {
    height: #{$height}px;
  }
}
// Forms ************
form {
  padding: 2em;
  border: 1px solid $bg-grey;
  border-radius: 4px;
}
button,
.button {
  background-color: $bg-primary;
  border: none;
  color: white;
  padding: 0.5em 2em;
  margin-top: 20px;
  text-decoration: none;
  margin: 4px 2px;
  min-width: 10em;
  font-family: OpenSansRegular;
  cursor: pointer;
  font-size: 1em;
  border-radius: 4px;
  transition-duration: 0.2s;
  transition-property: background-color;
  transition-timing-function: ease-in-out;
  &:hover {
    background-color: $bg-primary-lite;
  }
  &[type="submit"] {
    margin-top: 1.5em;
  }
}
input[type="text"],
input[type="file"],
select {
  width: 100%;
  padding: 10px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 1px solid $bg-grey;
  border-radius: 4px;
  font-family: OpenSans;
  font-size: 1em;
  outline: none;
  &:focus {
    border: 2px solid $bg-primary;
    padding: 9px 19px;
  }
}
label {
  margin-top: 10px;
  padding-bottom: 5px;
  font-size: 1em;
}
// h1..h6  ***************************************
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: OpenSansRegular;
}

.box {
  width: 100%;
  margin: 1.3em 0 1.3em 0;
  padding: 1em;
  border-radius: 4px;
}
.alert {
  background-color: #ffbbbf;
}
.success {
  background-color: #bcddb2;
}
.info {
  background-color: #9ab7e6;
}
// menu   ***************************************
nav > ul {
  list-style: none;
  margin: 0;
  width: 100vw;
  background: grey;
  display: flex;
  justify-content: flex-start;
}
nav > ul > li > a {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: white;
  font-family: OpenSans;
}
nav > ul > li > a:hover {
  background-color: $bg-primary;
}
@media all and (max-width: 800px) {
  nav > ul {
    flex-direction: column;

    padding: 0;
  }

  nav > ul > a {
    text-align: center;
    padding: 10px;
    border: 1px;
    border-style: solid;
    border-color: dimgrey;
  }
}
